let cnt = 1

let randomNumber = (min = 0, max = 1) => {
  return Math.floor(Math.random() * (max - min + 1) + min)
}

class Dashboard extends Component {
  _isMounted = false

  constructor(props) {
    super(props)

    this.state = {
      history: '',
      data: {
        dest: '0',
        type: '0',
        heartrate: DATA[0][0],
        power: DATA[0][1],
        cadence: DATA[0][2],
        distance: 0,
        speed: DATA[0][4],
        time: Math.round((DATA[0][5] / 3) * 60),
        gear: Math.floor(0 / 36 + 1),
      },
      weather: {
        temperature: randomNumber(28, 20),
        altitude: randomNumber(50, 1000),
        pressure: 1,
      },
      showExtra: true,
    }
  }

  loading = () => (
    <div className="animated fadeIn pt-1 text-center">Loading...</div>
  )

  history(history) {
    let param = ['heartrate', 'cadence', 'power', 'speed']
    let chart = {
      heartrate: [],
      cadence: [],
      power: [],
      speed: [],
    }
    let miniChart = {
      heartrate: [],
      cadence: [],
      power: [],
      speed: [],
    }
    let rev_history = history.reverse()
    let list = rev_history.slice(0, numElement)
    let miniList = rev_history.slice(0, numCardElement)

    list.reverse()
    miniList.reverse()

    for (let i = 0; i < list.length && i < numElement; i++) {
      for (let j = 0; j < param.length; j++) {
        chart[param[j]].push(JSON.parse(list[i])[param[j]])
        if (i < numCardElement) {
          miniChart[param[j]].push(JSON.parse(miniList[i])[param[j]])
        }
      }
    }

    let newState = {
      ...this.state,
      history: {
        chart: chart,
        miniChart: miniChart,
      },
    }
    this.setState(newState)
  }

  componentDidMount() {
    this._isMounted = true

    setInterval(() => {
      if (this._isMounted) {
        if (cnt >= DATA.length) cnt = 0

        let data = {
          dest: '0',
          type: '0',
          heartrate: DATA[cnt][0],
          power: DATA[cnt][1],
          cadence: DATA[cnt][2],
          distance: Math.round(DATA[cnt][3] * 1000 * 100) / 100,
          speed: Math.round(DATA[cnt][4] * 100) / 100,
          time: Math.trunc((DATA[cnt][5] / 3) * 60),
          gear: Math.floor(cnt / 36 + 1),
        }
        cnt++
        this.setState({ data })
      }
    }, 300)

    // aggiorna i valori del meteo
    setInterval(() => {
      if (this._isMounted) {
        let weather = {
          temperature: randomNumber(30, 20),
          altitude: randomNumber(50, 1000),
          pressure: randomNumber(5, 1),
        }
        this.setState({ weather })
      }
    }, 3000)
  }

  componentWillUnmount() {
    this._isMounted = false
  }

  render() {
    return (
      <article>
        <Row>
          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-info">
              <CardBody className="pb-0">
                <ButtonGroup id="card1" className="float-right">
                  <FaSpaceShuttle size={'1.5em'} />
                </ButtonGroup>
                <div className="text-value">{this.state.data.power}</div>
                <div>Power [W]</div>
              </CardBody>
              <div className="chart-wrapper" style={{ height: '60px' }}>
                <PowerCard
                  data={this.state.data}
                  history={this.state.history.miniChart}
                />
              </div>
            </Card>
          </Col>

          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-success">
              <CardBody className="pb-0">
                <ButtonGroup id="card2" className="float-right">
                  <GiCartwheel size={'1.5em'} />
                </ButtonGroup>
                <div className="text-value">{this.state.data.cadence}</div>
                <div>Cadence [rpm]</div>
              </CardBody>
              <div className="chart-wrapper" style={{ height: '60px' }}>
                <CadenceCard
                  data={this.state.data}
                  history={this.state.history.miniChart}
                />
              </div>
            </Card>
          </Col>

          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-warning">
              <CardBody className="pb-0">
                <ButtonGroup id="card3" className="float-right">
                  <GiSpeedometer size={'1.5em'} />
                </ButtonGroup>
                <div className="text-value">{this.state.data.speed}</div>
                <div>Speed [km/h]</div>
              </CardBody>
              <div className="chart-wrapper" style={{ height: '60px' }}>
                <SpeedCard
                  data={this.state.data}
                  history={this.state.history.miniChart}
                />
              </div>
            </Card>
          </Col>

          <Col xs="12" sm="6" lg="3">
            <Card className="text-white bg-danger">
              <CardBody className="pb-0">
                <ButtonGroup id="card4" className="float-right">
                  <FiActivity size={'1.5em'} />
                </ButtonGroup>
                <div className="text-value">{this.state.data.heartrate}</div>
                <div>Heartrate [bpm]</div>
              </CardBody>
              <div className="chart-wrapper" style={{ height: '60px' }}>
                <HRCard
                  data={this.state.data}
                  history={this.state.history.miniChart}
                />
              </div>
            </Card>
          </Col>
        </Row>

        <Row>
          <Col>
            <Card>
              <CardBody>
                <div
                  className="chart-wrapper"
                  style={{ height: `370px`, marginTop: 0 }}
                >
                  <MainChart
                    data={this.state.data}
                    history={this.state.history.chart}
                  />
                </div>
              </CardBody>
            </Card>
          </Col>
        </Row>

        <Extra
          showExtra={this.state.showExtra}
          gear={this.state.data.gear}
          distance={this.state.data.distance}
          time={this.state.data.time}
          weather={this.state.weather}
        />
      </article>
    )
  }
}

export default Dashboard
